body {
  font-family: Arial, sans-serif;
}

.people {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 200px);
  justify-content: center;
  gap: 40px 30px;
  padding: 30px 30px 155px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    --columns: 4;
  }
}

@media (width >= 1500px) {
  .people {
    --columns: 4;
  }
}

@media (width <= 500px) {
  .people {
    --columns: 2;
  }
}

.person {
  padding: 0;
  position: relative;
}

.person__photo {
  margin: 0 auto;
  position: relative;
}

.person__photo img {
  border-radius: 200px;
  width: 200px;
  height: 200px;
  transition: transform 1s;
  display: block;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 20px;
  padding: 7px 12px;
  font-weight: 700;
  position: absolute;
  top: 84px;
  right: 10px;
}

.person__details {
  box-sizing: border-box;
  text-align: center;
  width: 200px;
  margin: 0 auto;
  padding: 8px 13px;
  position: relative;
}

.person__name {
  color: #2f4f4f;
  font-weight: 700;
  display: block;
}

.person__extra {
  opacity: 0;
  font-weight: 400;
  position: absolute;
  bottom: -20px;
  left: 13px;
  right: 13px;
}

.person:hover .person__details .person__extra {
  opacity: 1;
}

.person:after {
  content: "";
  opacity: 0;
  z-index: 0;
  background-color: #eee;
  width: 220px;
  height: 287px;
  transition: opacity .3s;
  position: absolute;
  top: 0;
  left: -10px;
}

.person:hover:after {
  opacity: 1;
}

.person > * {
  z-index: 1;
  position: relative;
}
